import type { GeoLocation } from '@/lib/types';

/**
 * Asynchronously retrieves the current geographical location using the browser's Geolocation API.
 *
 * @returns A promise that resolves to a GeoLocation object containing latitude and longitude.
 * @throws {Error} If Geolocation is not supported or permission is denied, or if there's an error retrieving the location.
 */
export function getCurrentLocation(): Promise<GeoLocation> {
  return new Promise((resolve, reject) => {
    if (!navigator.geolocation) {
      reject(new Error('Geolocation is not supported by your browser.'));
      return;
    }

    navigator.geolocation.getCurrentPosition(
      (position) => {
        resolve({
          latitude: position.coords.latitude,
          longitude: position.coords.longitude,
        });
      },
      (error) => {
        switch (error.code) {
          case error.PERMISSION_DENIED:
            reject(new Error('User denied the request for Geolocation.'));
            break;
          case error.POSITION_UNAVAILABLE:
            reject(new Error('Location information is unavailable.'));
            break;
          case error.TIMEOUT:
            reject(new Error('The request to get user location timed out.'));
            break;
          default: // error.UNKNOWN_ERROR and any other codes
             reject(new Error('An unknown error occurred while retrieving location. Code: ' + error.code));
            break;
        }
      },
      {
        enableHighAccuracy: true, // Request more accurate position
        timeout: 10000, // 10 seconds timeout
        maximumAge: 0, // Force fresh location data
      }
    );
  });
}
