/**
 * Asynchronously captures a selfie image using the device's camera via the browser's MediaDevices API.
 * Returns the image as a base64 encoded data URL string.
 *
 * @returns A promise that resolves to a base64 encoded string representing the captured image (e.g., "data:image/png;base64,...").
 * @throws {Error} If camera access is not supported, denied, or fails.
 */
export function captureSelfie(): Promise<string> {
  return new Promise(async (resolve, reject) => {
    if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
      reject(new Error('Camera access is not supported by your browser.'));
      return;
    }

    try {
      const stream = await navigator.mediaDevices.getUserMedia({
        video: {
          facingMode: 'user', // Prefer front camera
          width: { ideal: 320 }, // Request a reasonable size
          height: { ideal: 240 }
        },
        audio: false,
      });

      const video = document.createElement('video');
      video.srcObject = stream;
      video.setAttribute('playsinline', 'true'); // Required for iOS Safari
      video.play();


      // Wait for the video to be ready to capture a frame
      video.addEventListener('loadeddata', () => {
          // Use a canvas to capture a frame
          const canvas = document.createElement('canvas');
           // Set canvas dimensions based on video track settings or video element dimensions
          const track = stream.getVideoTracks()[0];
          const settings = track.getSettings();
          canvas.width = settings.width || video.videoWidth || 320;
          canvas.height = settings.height || video.videoHeight || 240;


          const context = canvas.getContext('2d');
          if (!context) {
            stream.getTracks().forEach(track => track.stop()); // Stop stream
            reject(new Error('Could not get canvas context.'));
            return;
          }

          // Draw the current video frame onto the canvas
          context.drawImage(video, 0, 0, canvas.width, canvas.height);

          // Stop the video stream tracks
          stream.getTracks().forEach(track => track.stop());

          // Convert the canvas image to a base64 data URL (PNG format)
          const dataUrl = canvas.toDataURL('image/png');
          resolve(dataUrl);
      });

       video.addEventListener('error', (e) => {
            stream.getTracks().forEach(track => track.stop());
            reject(new Error(`Video playback error: ${e}`));
        });

    } catch (error: any) {
        let message = 'Failed to access camera.';
         if (error.name === 'NotAllowedError' || error.name === 'PermissionDeniedError') {
            message = 'Camera permission denied. Please allow camera access in your browser settings.';
         } else if (error.name === 'NotFoundError' || error.name === 'DevicesNotFoundError') {
             message = 'No camera found. Please ensure a camera is connected and enabled.';
         } else if (error.name === 'NotReadableError' || error.name === 'TrackStartError') {
             message = 'Camera is already in use or cannot be accessed.';
         } else if (error.name === 'OverconstrainedError' || error.name === 'ConstraintNotSatisfiedError') {
             message = 'Camera does not support requested constraints (e.g., resolution).';
         } else if (error.name === 'TypeError') {
              message = 'getUserMedia or constraints are not valid.';
         }
        reject(new Error(`${message} (${error.name})`));
    }
  });
}
