'use client';

import type { ChangeEvent } from 'react';
import { useState, useEffect } from 'react';
import { useForm, Controller } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Button } from '@/components/ui/button';
import { Card, CardHeader, CardTitle, CardDescription, CardContent, CardFooter } from '@/components/ui/card';
import { Label } from '@/components/ui/label';
import { Input } from '@/components/ui/input'; // Although not directly used for file, kept for consistency if needed later
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { useToast } from '@/hooks/use-toast';
import { getCurrentLocation } from '@/services/geo-location';
import { captureSelfie as captureSelfieService } from '@/services/camera'; // Renamed import
import type { GeoLocation, AttendanceRecord } from '@/lib/types';
import Image from 'next/image';
import { Camera, MapPin, Send, Loader2, User } from 'lucide-react';

// Mock function to simulate saving attendance
async function saveAttendance(data: Omit<AttendanceRecord, 'id' | 'timestamp'>): Promise<AttendanceRecord> {
  console.log('Saving attendance:', data);
  // Simulate API call delay
  await new Promise(resolve => setTimeout(resolve, 1500));
  return {
    ...data,
    id: Math.random().toString(36).substring(7),
    timestamp: new Date(),
  };
}

const attendanceSchema = z.object({
  userId: z.string().min(1, 'User ID is required'), // Added User ID field
  userName: z.string().min(1, 'User Name is required'), // Added User Name field
  selfie: z.string().min(1, 'Selfie is required.'),
  location: z.object({
    latitude: z.number(),
    longitude: z.number(),
  }).optional(), // Make location optional in schema, set dynamically
});

type AttendanceFormData = z.infer<typeof attendanceSchema>;

export default function AttendancePage() {
  const { toast } = useToast();
  const [location, setLocation] = useState<GeoLocation | null>(null);
  const [locationError, setLocationError] = useState<string | null>(null);
  const [selfiePreview, setSelfiePreview] = useState<string | null>(null);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [isCapturingSelfie, setIsCapturingSelfie] = useState(false);
  const [isFetchingLocation, setIsFetchingLocation] = useState(false);

  const form = useForm<AttendanceFormData>({
    resolver: zodResolver(attendanceSchema),
    defaultValues: {
      userId: '', // Initialize user ID
      userName: '', // Initialize user name
      selfie: '',
      location: undefined,
    },
  });

  const fetchLocation = async () => {
    setIsFetchingLocation(true);
    setLocationError(null);
    try {
      const currentLocation = await getCurrentLocation();
      setLocation(currentLocation);
      form.setValue('location', currentLocation); // Set location in form
      console.log('Location fetched:', currentLocation);
    } catch (error) {
      console.error('Error fetching location:', error);
      setLocationError('Failed to get location. Please ensure location services are enabled.');
      toast({
        variant: 'destructive',
        title: 'Location Error',
        description: 'Could not retrieve your current location.',
      });
    } finally {
      setIsFetchingLocation(false);
    }
  };

  // Fetch location on component mount
  useEffect(() => {
    fetchLocation();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleCaptureSelfie = async () => {
    setIsCapturingSelfie(true);
    try {
      // In a real app, this would interact with the device camera API.
      // We'll use a placeholder service function for now.
      const selfieData = await captureSelfieService();
      setSelfiePreview(selfieData);
      form.setValue('selfie', selfieData, { shouldValidate: true });
      console.log('Selfie captured');
    } catch (error) {
      console.error('Error capturing selfie:', error);
      toast({
        variant: 'destructive',
        title: 'Selfie Error',
        description: 'Could not capture selfie.',
      });
    } finally {
      setIsCapturingSelfie(false);
    }
  };

  const onSubmit = async (data: AttendanceFormData) => {
    if (!location) {
      toast({
        variant: 'destructive',
        title: 'Missing Location',
        description: 'Cannot submit attendance without location.',
      });
      return;
    }

    if (!data.selfie) {
        toast({
            variant: 'destructive',
            title: 'Missing Selfie',
            description: 'Cannot submit attendance without a selfie.',
          });
        return;
    }


    setIsSubmitting(true);
    console.log('Submitting attendance data:', data);

    try {
      const attendanceDataToSave: Omit<AttendanceRecord, 'id' | 'timestamp'> = {
        userId: data.userId, // Include user ID
        userName: data.userName, // Include user name
        selfie: data.selfie,
        location: location, // Use the state location
      };

      const savedRecord = await saveAttendance(attendanceDataToSave);

      // Add to local storage for history page simulation
      const history = JSON.parse(localStorage.getItem('attendanceHistory') || '[]');
      history.push(savedRecord);
      localStorage.setItem('attendanceHistory', JSON.stringify(history));

      toast({
        title: 'Attendance Submitted',
        description: `Attendance marked successfully at ${savedRecord.timestamp.toLocaleTimeString()}.`,
      });
      // Reset form and previews
      form.reset();
      setSelfiePreview(null);
      // Optionally refetch location or keep the existing one
      // fetchLocation();
    } catch (error) {
      console.error('Error saving attendance:', error);
      toast({
        variant: 'destructive',
        title: 'Submission Failed',
        description: 'Could not save your attendance. Please try again.',
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="flex justify-center items-center">
      <Card className="w-full max-w-md shadow-lg">
        <CardHeader>
          <CardTitle className="text-2xl font-bold text-center text-primary">Mark Attendance</CardTitle>
          <CardDescription className="text-center">Submit your attendance with a selfie and current location.</CardDescription>
        </CardHeader>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)}>
            <CardContent className="space-y-6">
               <FormField
                control={form.control}
                name="userId"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel className="flex items-center gap-1"><User className="h-4 w-4" /> User ID</FormLabel>
                    <FormControl>
                      <Input placeholder="Enter your User ID" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <FormField
                control={form.control}
                name="userName"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel className="flex items-center gap-1"><User className="h-4 w-4" /> User Name</FormLabel>
                    <FormControl>
                      <Input placeholder="Enter your Name" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />

              {/* Selfie Section */}
               <FormField
                control={form.control}
                name="selfie"
                render={({ field }) => (
                    <FormItem>
                         <FormLabel className="flex items-center gap-1"><Camera className="h-4 w-4" /> Selfie</FormLabel>
                        <div className="flex items-center gap-4">
                             <Button
                                type="button"
                                variant="outline"
                                onClick={handleCaptureSelfie}
                                disabled={isCapturingSelfie || isSubmitting}
                                className="flex-shrink-0"
                             >
                                {isCapturingSelfie ? (
                                    <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                                ) : (
                                    <Camera className="mr-2 h-4 w-4" />
                                )}
                                Capture Selfie
                            </Button>
                            {selfiePreview && (
                            <div className="w-20 h-20 rounded-md overflow-hidden border border-muted">
                                <Image
                                src={selfiePreview}
                                alt="Selfie Preview"
                                width={80}
                                height={80}
                                className="object-cover w-full h-full"
                                />
                            </div>
                             )}
                        </div>
                         <FormMessage />
                    </FormItem>
                 )}
                />


              {/* Location Section */}
              <div className="space-y-2">
                <Label className="flex items-center gap-1"><MapPin className="h-4 w-4" /> Location</Label>
                <div className="p-3 bg-secondary rounded-md border text-sm">
                  {isFetchingLocation ? (
                     <div className="flex items-center text-muted-foreground">
                        <Loader2 className="mr-2 h-4 w-4 animate-spin" /> Fetching location...
                     </div>
                  ) : location ? (
                    <p>
                      Lat: {location.latitude.toFixed(4)}, Lon: {location.longitude.toFixed(4)}
                    </p>
                  ) : (
                    <p className="text-destructive">{locationError || 'Location not available.'}</p>
                  )}
                </div>
                {!location && !isFetchingLocation && (
                    <Button type="button" variant="outline" size="sm" onClick={fetchLocation}>
                        Retry Fetching Location
                    </Button>
                )}
              </div>
            </CardContent>
            <CardFooter>
              <Button
                type="submit"
                className="w-full bg-accent hover:bg-accent/90"
                disabled={isSubmitting || isFetchingLocation || isCapturingSelfie || !location || !form.formState.isValid}
              >
                {isSubmitting ? (
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                ) : (
                  <Send className="mr-2 h-4 w-4" />
                )}
                Submit Attendance
              </Button>
            </CardFooter>
          </form>
        </Form>
      </Card>
    </div>
  );
}
